import React from "react";
import GlobalStyle from "./globalStyles";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Navbar } from "./components";
import { Home, About, Contact, Error } from "./pages";

const PageItems = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/about",
    component: About,
  },
  {
    path: "/contact",
    component: Contact,
  },
  {
    path: "",
    component: Error,
  },
];

function App() {
  return (
    <Router>
      <GlobalStyle />
      <Navbar />
      <Switch>
        {PageItems.map((pageitem) => {
          return (
            <Route exact path={pageitem.path} component={pageitem.component} />
          );
        })}
      </Switch>
    </Router>
  );
}

export default App;
