import React, { useState } from "react";
import GlobalStyle from "../../globalStyles";
import {
  Header,
  Container,
  Nav,
  Logo,
  Burger,
  Ul,
  Li,
  NavLinks,
} from "./Navbar.elements";
import NavbarItems from "./NavbarItems";

const Navbar = () => {
  const [click, setClick] = useState(false);
  const preventClick = () => setClick(false);
  return (
    <>
      <GlobalStyle />
      <Header>
        <Container>
          <Nav>
            <Logo to="/" onClick={preventClick}>
              Logo
            </Logo>
            <Burger onClick={() => setClick(!click)}>
              {click ? (
                <svg viewBox="0 0 10 8" width="22">
                  <path
                    d="M3 1 l5 5 M3 6 l5 -5"
                    stroke="#d6e0f0"
                    fill="none"
                    stroke-width="1.5"
                    stroke-linecap="round"
                  />
                </svg>
              ) : (
                <svg viewBox="0 0 10 8" width="22">
                  <path
                    d="M3 1 h4 M1 4 h8 M3 7 h4"
                    stroke="#d6e0f0"
                    stroke-width="1.5"
                    stroke-linecap="round"
                  />
                </svg>
              )}
            </Burger>
            <Ul click={click}>
              {NavbarItems.map((navitem) => {
                return (
                  <Li>
                    <NavLinks exact onClick={preventClick} to={navitem.to}>
                      {navitem.text}
                    </NavLinks>
                  </Li>
                );
              })}
            </Ul>
          </Nav>
        </Container>
      </Header>
    </>
  );
};

export default Navbar;
