import styled from "styled-components";
import { Link, NavLink } from "react-router-dom";

export const Header = styled.header`
  position: fixed;
  width: 100%;
  padding: 1.5rem 0;
  background: #393b44;
  z-index: 1;
`;

export const Container = styled.div`
  max-width: 100rem;
  margin: 0 auto;
  padding: 0 1rem;
`;

export const Nav = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

export const Logo = styled(Link)`
  color: #d6e0f0;
  font-size: 2rem;
  z-index: 2;
`;

export const Burger = styled.div`
  z-index: 2;
  line-height: 0;
  cursor: pointer;
  display: none;

  @media (max-width: 700px) {
    display: block;
  }
`;

export const Ul = styled.ul`
  display: flex;

  @media (max-width: 700px) {
    background: #393b44;
    position: fixed;
    left: 0;
    bottom: ${(props) => (props.click ? "0" : "-100vh")};
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100vw;
    height: 100vh;
    transition: bottom 0.3s ease-in-out;
  }
`;

export const Li = styled.li`
  padding: 0 2rem;

  &:last-child {
    padding-right: 0;

    @media (max-width: 700px) {
      padding-right: 2rem;
    }
  }
`;

export const NavLinks = styled(NavLink)`
  color: #d6e0f0;
  font-size: 1.5rem;
  position: relative;

  &::after {
    content: "";
    width: 100%;
    height: 2px;
    border-radius: 50px;
    background: #d6e0f0;
    position: absolute;
    bottom: -0.8rem;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease-in-out;
  }

  &.active::after,
  &:hover::after {
    transform: scaleX(1);
  }
`;
